<template>
  <PageWrapper dense contentFullHeight fixedHeight contentClass="flex" style="position: relative">
    <BasicTable @register="registerTable" @resize-column="(w, col) => (col.width = w)">
      <template #toolbar>
        <a-button class="yellow-btn" :preIcon="IconEnum.EXPORT" @click="handleExport">{{
          t('common.action.export')
        }}</a-button>
      </template>
    </BasicTable>
  </PageWrapper>
</template>
<script lang="ts" setup>
  import { BasicTable, useTable } from '@/components/Table';
  import {
    fullColumns,
    dtlColumns,
    searchFormSchema,
    getPowerContrastData,
    exportExcel,
  } from './data';
  import { useRouter } from 'vue-router';
  import { onMounted, unref, reactive, ref } from 'vue';
  import * as DateUtil from '/@/utils/dateUtil';

  import { useMessage } from '@/hooks/web/useMessage';
  import { useI18n } from '@/hooks/web/useI18n';
  import { IconEnum } from '@/enums/appEnum';
  import { exportExcelFile } from '/@/utils/file/download';
  import { PageWrapper } from '/@/components/Page';
  import { BasicColumn } from '/@/components/Table';
  import { assign } from 'lodash-es';

  const { t } = useI18n();
  const { createConfirm, createMessage } = useMessage();

  //导出
  const queryParam: any = reactive({});
  function handleExport() {
    createConfirm({
      title: t('common.message.confirmTitle'),
      iconType: 'warning',
      content: t('common.message.exportMessage'),
      async onOk() {
        Object.assign(queryParam, getForm().getFieldsValue());
        //针对穿透赋值后存在时分秒
        if (queryParam.dateCycle == 'month' && queryParam.startMonth.length != 7) {
          queryParam.startMonth = DateUtil.formatToDate(queryParam.startMonth, 'YYYY-MM');
          queryParam.endMonth = DateUtil.formatToDate(queryParam.endMonth, 'YYYY-MM');
        }
        queryParam.filename = '电量对比统计.xls';
        queryParam.excelList = columns;
        await exportExcel(queryParam).then((res) => {
          exportExcelFile(res?.data, queryParam.filename);
        });
        queryParam.filename = null;
        queryParam.excelList = null;
        createMessage.success(t('common.exportSuccessText'));
      },
    });
  }

  //-----------------跳转相关--------------------
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = route.query;

  const getResultData = async () => {
    let params = getForm().getFieldsValue();
    //针对穿透赋值后存在时分秒
    if (params.dateCycle == 'month' && params.startMonth.length != 7) {
      params.startMonth = DateUtil.formatToDate(params.startMonth, 'YYYY-MM');
      params.endMonth = DateUtil.formatToDate(params.endMonth, 'YYYY-MM');
    }
    await queryColumns();
    let dataObj = await getPowerContrastData(params);
    return dataObj;
  };
  onMounted(async () => {
    if (query) {
      const values = getForm().getFieldsValue();
      //针对穿透赋值
      if (query.startDate) {
        values.dateCycle = query.dateType;
        values.startMonth = query.startDate;
        values.endMonth = query.endDate;
      }
      if (query.stationId != null) {
        values.stationId = query.stationId;
      }
      if (query.orgId != null) {
        values.orgId = query.orgId;
      }
      getForm().setFieldsValue(values);
    }
  });

  const columns: BasicColumn[] = ref([]);
  const queryColumns = async () => {
    columns.value = [];
    const values = getForm().getFieldsValue();
    if (
      values.dateCycle === 'month' &&
      DateUtil.formatToDate(values.startMonth, 'YYYY-MM') ==
        DateUtil.formatToDate(values.endMonth, 'YYYY-MM')
    ) {
      assign(columns.value, fullColumns);
    } else {
      assign(columns.value, dtlColumns);
    }
  };

  const [registerTable, { getForm }] = useTable({
    title: '电量对比统计分析',
    api: getResultData,
    columns,
    formConfig: searchFormSchema,
    useSearchForm: true,
    bordered: true,
    pagination: false,
    showIndexColumn: true,
    showTableSetting: true,
    tableSetting: {
      redo: false,
      size: false,
      setting: false,
      fullScreen: false,
    },
    afterFetch: function (data) {
      const count = {};
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        if (count[element.stationOrganId]) {
          count[element.stationOrganId]++;
        }
        if (count[element.stationId]) {
          count[element.stationId]++;
        }

        if (!count[element.stationOrganId]) {
          count[element.stationOrganId] = 1;
        }
        if (!count[element.stationId]) {
          count[element.stationId] = 1;
        }
      }
      for (let index = 0; index < data.length; index++) {
        const element = data[index];
        for (const key in count) {
          if (element.stationOrganId === key) {
            element.rowOrgSpan = count[key];
            count[key] = 0;
          }
          if (element.stationId === key) {
            element.rowStationSpan = count[key];
            count[key] = 0;
          }
        }
      }
      return data;
    },
  });
</script>
